@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
  /* background-image: url('../../assets//bg-home.jpg'); */
  /* background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0) 40%, #000 100%);
  background: -moz-linear-gradient(to bottom, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0) 40%, #000 100%);
  background: -o-linear-gradient(to bottom, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0) 40%, #000 100%);
  background: -webkit-linear-gradient(to bottom, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0) 40%, #000 100%); */
}

.header-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 85%;
  position: relative;
}

.header-container-info {
  margin-top: -6rem;
}

.header-container-info h5 {
  color: var(--color-black);
}

.header-container-info h2 {
  color: var(--color-black);
}

/* ============================== CTA =================================== */
.cta {
  margin-top: 2.5rem;
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  justify-content: center;
}

/* ============================== Header Social =================================== */
.header-socials {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 1rem;
  gap: 1rem;
  position: absolute;
  left: 1px;
  margin-top: 2rem;
}
/* ============================== Me =================================== */
.me {
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 24rem;
  position: absolute;
  left: calc(80% - 11rem);
  border-radius: 3rem;
  margin-top: 4rem;
  margin-bottom: 6rem;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem;
  animation: go-up 3s;
  animation-duration: 5s ease-in-out;
}

@keyframes go-up {
  0% {
    transform: translateY(100px);
  }

  100% {
    transform: translateY(0);
  }
}

/* ============================== Scroll Down =================================== */
.scroll-down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/*  ------------------------------ Media Queries = Medium Devices ----------------------------------  */
@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
  
}

/*  ------------------------------ Media Queries = Small Devices ----------------------------------  */
@media screen and (max-width: 600px) {
  header {
    height: 100vh;
  }

  .me {
    display: none;
  }

  .header-socials,
  .scroll-down {
    display: none;
  }
}
nav {
  background: rgba(0, 0, 0, 0.3);
  width: -webkit-max-content;
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  border-radius: 3rem;
  -webkit-backdrop-filter: blur(15px);
          backdrop-filter: blur(15px);
}

nav a {
  background: transparent;
  padding: 0.4rem;
  border-radius: 50%;
  display: flex;
  color: var(--color-light);
  font-size: 1.1rem;
}

nav a:hover {
  background: rgba(0, 0, 0, 0.4);
}

nav a.active {
  background: var(--color-primary);
  color: var(--color-white);
}
.about-container {
  display: grid;
  grid-template-columns: 35% 50%;
  grid-gap: 15%;
  gap: 15%;
}

.container-wrapper {
  animation: showComponent 3s;
}

#about h5 {
  color: #000;
}

.about-me {
  width: 200px;
  height: 200px;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(45deg, transparent, var(--color-primary), transparent);
  display: grid;
  place-items: center;
  cursor: pointer;
}

.about-me-img {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition);
}

.about-me-img img {
  width: 190px;
  height: 190px;
}

.about-me-img:hover {
  transform: rotate(0);
}

.about-cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.about-card {
  opacity: 0.8;
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 1rem;
  text-align: center;
  border-color: var(--color-primary-variant);
  cursor: pointer !important;
}

.about-card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.about-icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.about-card h5 {
  font-size: 0.95rem;
}

.about-card small {
  font-size: 0.7rem;
  color: #121212;
}

.about-content p {
  margin: 2rem 0 2.6rem;
  color: #000;
  text-align: justify;
}

.btn-about-me {
  background: var(--color-primary);
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: #fff;
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
}

/*  ------------------------------ Media Queries = Medium Devices ----------------------------------  */
@media screen and (max-width: 1024px) {
  .about-container {
    grid-template-columns: 1fr;
    grid-gap: 0;
    gap: 0;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .about-me {
    width: 50%;
    margin: 2rem auto 4rem;
  }

  .about-content p {
    margin: 1rem 0 1.5rem;
    text-align: center;
  }
}

/*  ------------------------------ Media Queries = Small Devices ----------------------------------  */
@media screen and (max-width: 600px) {
  .about-me {
    width: 65%;
    margin: 0 auto 3rem;
    height: 320px
  }

  .about-me-img img {
    width: 100%;
    height: 300px;
  }

  .about-cards {
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
  }

  .about-content {
    text-align: center;
  }

  .about-content p {
    margin: 1.5rem 0;
    text-align: justify;
  }
}

@keyframes showComponent {
  0% {
    transform: translateY(100px);
  }

  100% {
    transform: translateY(0);
  }
}


#experience h5 {
  color: #000;
}

.experience-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2rem;
  gap: 2rem;
}

.experience-container > div {
  background: transparent;
  padding: 2.4rem 5rem;
  border-radius: 2rem;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.experience-container > div:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.experience-container > div h3 {
  text-align: center;
  margin-bottom: 2rem;
  color: var(--color-primary);
}

.experience-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.experience-details {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
}

.experience-details-icon {
  margin-top: 6px;
  color: var(--color-primary);
}

.titleTech {
  color: #000;
}

@media screen and (max-width: 1024px) {
  .experience-container {
    grid-template-columns: 1fr;
  }

  .experience-container > div {
    width: 80%;
    padding: 2rem;
    margin: 0 auto;
  }

  .experience-content {
    padding: 1rem;
  }
}

/*  ------------------------------ Media Queries = Small Devices ----------------------------------  */
@media screen and (max-width: 600px) {
  .experience-container {
    grid-gap: 1rem;
    gap: 1rem;
  }

  .experience-container > div {
    width: 100%;
    padding: 2rem 1rem;
  }
}

#services h5 {
  color: #000;
}

.services-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  gap: 2rem;
}

.service {
  background: transparent;
  color: #000;
  border-radius: 0 0 2rem 2rem;
  border: 1px solid var(--color-primary);
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  transition: var(--transition);
}

.service-head {
  background: var(--color-primary);
  padding: 2rem;
  border-radius: 0 0 2rem 2rem;
  box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1)
}

.service-head h3 {
  color: var(--color-bg);
  font-size: 1rem;
  text-align: center;
}

.service-list {
  padding: 2rem;
}

.service-list li {
  display: flex;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 0.8rem;
  align-items: center;
  justify-content: center;
}

.service-list-icon {
  color: var(--color-primary);
  margin-top: 2px;
}

.service-list p {
  font-size: 0.9rem;
}


/*  ------------------------------ Media Queries = Medium Devices ----------------------------------  */
@media screen and (max-width: 1024px) {
  .services-container {
    grid-template-columns: 1fr 1fr;
    grid-gap: 2rem;
    gap: 2rem;
  }

  .service {
    height:  auto;
  }
}

/*  ------------------------------ Media Queries = Small Devices ----------------------------------  */
@media screen and (max-width: 600px) {
  .services-container {
    grid-template-columns: 1fr;
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }
}
#portifolio h5 {
  color: #000;
}

.portifolio-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2.5rem;
  gap: 2.5rem;
}

.portifolio-item {
  background: transparent;
  padding: 1.2rem;
  border-radius: 2rem;
  border: 1px solid var(--color-primary-variant);
  transition: var(--transition)
}

.portifolio-item h3 {
  color: #000;
}

.portifolio-item:hover {
  border-color: var(--color-primary-variant);
  background: transparent;
}

.portifolio-item-img {
  border-radius: 1.5rem;
  overflow: hidden;
}

.portifolio-item h3 {
  margin: 1.2rem 0rem 2rem;
}

.link-repos {
  margin-top: 1.5rem;
  color: var(--color-primary-variant);
}

.coffe-link {
  font-size: 0.8rem;
  margin-top: -1.5rem;
  margin-bottom: 0.5rem;
  text-align: justify;
}

/*  ------------------------------ Media Queries = Medium Devices ----------------------------------  */
@media screen and (max-width: 1024px) {
  .portifolio-container {
    grid-template-columns: 1fr 1fr;
    grid-gap: 1.2rem;
    gap: 1.2rem;
  }
  
}

/*  ------------------------------ Media Queries = Small Devices ----------------------------------  */
@media screen and (max-width: 600px) {
  .portifolio-container {
    grid-template-columns: 1fr;
    grid-gap: 1rem;
    gap: 1rem;
  }
}



#contact h5 {
  color: var(--color-black);
}

.contact-container {
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  grid-gap: 12%;
  gap: 12%;
}

.contact-options {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}

.contact-option {
  background: transparent;
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid var(--color-primary-variant);
  transition: var(--transition);
}

.contact-option svg {
  color: var(--color-black);  
}

.contact-option h4 {
  color: var(--color-black);
}

.contact-option:hover {
  background: transparent;
  border-color: var(--color-primary);
}

.contact-option-icon {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.contact-option a {
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 0.8rem;
}

.btn-submit-form {
  max-width: 120px;
  width: 100%;
  font-size: 14px;
  background-color: var(--color-primary);
  height: 45px;
  padding: 0 1rem;
  border-radius: 10px;
  cursor: pointer;
  color: #fff;
}

/*========================= Form ========================= */
form {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}

input, textarea {
  width: 100%;
  padding: 1rem;
  border-radius: 0.5rem;
  background-color: transparent;
  border: 2px solid var(--color-primary-variant);
  resize: none;
  color: var(--color-black);
}

/*  ------------------------------ Media Queries = Medium Devices ----------------------------------  */
@media screen and (max-width: 1024px) {
  .contact-container {
    grid-template-columns: 1fr;
    grid-gap: 2rem;
    gap: 2rem;
  }
}

/*  ------------------------------ Media Queries = Small Devices ----------------------------------  */
@media screen and (max-width: 600px) {
  .contact-container {
    width: var(--container-width-sm);
  }
}




